/* eslint-disable camelcase */
/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import {
  Button,
  FormGroup,
  Input,
  Label,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import instance from '../../helpers/axiosInstance';
import IntlMessages from '../../helpers/IntlMessages';
import { getCurrentUser } from '../../helpers/Utils';
import StateButton from '../StateButton';

const AddJudgeModal = ({ setOpenAddModal, id }) => {
  const [formValues, setFormValues] = useState({
    hackathon_id: id,
    name: '',
    job_title: '',
    company: '',
    profile_url: '',
    about: '',
  });

  const { access_token } = getCurrentUser();

  const handleChange = (event, key, value = false) => {
    setFormValues({
      ...formValues,
      [key]: value ? event : event.target.value,
    });
  };

  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const addJudge = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/addJudges',
            data: { judges: { ...newData } },
            headers: {
              Authorization: `Bearer ${access_token}`,
            },
          });

          const { data } = response;
          if (data.success) {
            setFormValues({ ...formValues, ...newData });
            success('Judge added successfully!');
          } else {
            failed('Failed to add judge!');
          }
        } catch (error) {
          console.log(error);
          failed('Failed to add judge!');
        }
      };

      addJudge();
    });
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.application-add-judge-title" />
        </h2>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <span className="h3">
            <i className="simple-icon-cloud-upload mr-2" />
          </span>
          <span className="font-weight-semibold mb-0">Upload an image</span>
        </FormGroup>

        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.judge" />
          </Label>
          <Input
            type="text"
            value={formValues.name}
            onChange={(event) => handleChange(event, 'name')}
          />
        </FormGroup>

        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.judge-social-links" />
          </Label>
          <Input
            type="text"
            value={formValues.profile_url}
            onChange={(event) => handleChange(event, 'profile_url')}
          />
        </FormGroup>

        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.judge-description" />
          </Label>
          <Input
            type="textarea"
            rows="6"
            value={formValues.about}
            onChange={(event) => handleChange(event, 'about')}
          />
        </FormGroup>
      </ModalBody>

      <ModalFooter>
        <StateButton
          id="add-judge-button"
          color="secondary"
          onClick={() => handleSubmit(formValues)}
          next={() => window.location.reload()}
        >
          <IntlMessages id="challenges.modal.application-add" />
        </StateButton>

        <Button color="danger" onClick={() => setOpenAddModal(false)}>
          <IntlMessages id="challenges.modal.application-cancel" />
        </Button>
      </ModalFooter>
    </>
  );
};

export default AddJudgeModal;
